.card {
  display: flex;
  position: relative;
  justify-content: center;
  align-items: center;
  background: #FFFFFF;
  border: 1px solid #F8F8F8;
  box-sizing: border-box;
  width: 29vw;
  height: 14vw;
  overflow: hidden;
  font-size: 0;
  cursor: pointer;
  &:hover {
    z-index: 99;
    box-shadow: 0px 0px 41px -16px rgba(0,0,0,0.94); }
  &__container {
    width: 90%;
    height: 90%;
    &__top {
      font-family: Lato Regular;
      &__name {
        font-size: 1.8vw;
        line-height: 2.8vw;
        color: #000000;
        margin-top: 0;
        margin-bottom: 10px; }
      &__date {
        font-size: 1.2vw;
        line-height: 1.5vw;
        color: #AEAEAE; } }
    &__bottom {
      position: absolute;
      bottom: 7%;
      display: flex;
      justify-content: center;
      align-items: center;
      &__download-text {
        font-family: Lato Bold;
        font-size: 1vw;
        line-height: 1vw;
        margin-right: 5px;
        opacity: 0;
        transition: 0.5s;
        & a {
          color: #00A88E;
          cursor: pointer;
          text-decoration: none; } }
      &__download-icon {
        position: absolute;
        left: 0%;
        background-image: url("../../assets/images/downloadIcon.png");
        background-repeat: no-repeat;
        background-size: 1vw 1vw;
        width: 1vw;
        height: 1vw;
        transition: 0.5s; } }
    &__preview {
        position: absolute;
        bottom: 0;
        right: -35%;
        width: 35%;
        height: 40%;
        transition: 0.5s;
        &__link {
          &__icon {
            background-repeat: no-repeat;
            background-size: 100% 100%;
            width: 100%;
            height: 100%; } } } } }

.download-text__active {
  opacity: 1; }

.download-icon__active {
  left: 100%; }

.preview__active {
  right: 0; }


@media (max-width: 979px) {
  .card {
    width: 44.792vw;
    height: 19.531vw;
    &:hover {
      box-shadow: none; }
    &__container {
      height: 85%;
      &__top {
        &__name {
          font-size: 2.344vw;
          line-height: 2.604vw;
          margin-bottom: 10px; }
        &__date {
          font-size: 1.823vw;
          line-height: 2.344vw; } }
      &__bottom {
        &__download-text {
          font-size: 1.563vw;
          line-height: 2.083vw; }
        &__download-icon {
          background-size: 1.563vw 1.563vw;
          width: 1.563vw;
          height: 1.563vw; } } } } }

@media (max-width: 640px) {
  .card {
    width: 93.750vw;
    height: 42.188vw;
    &__container {
      height: 85%;
      &__top {
        &__name {
          font-size: 5vw;
          line-height: 8.313vw;
          margin-bottom: 2vw; }
        &__date {
          font-size: 3vw;
          line-height: 5.250vw; } }
      &__bottom {
        bottom: 10%;
        &__download-icon {
          background-size: 2.806vw 2.806vw;
          width: 2.806vw;
          height: 2.806vw; } } } } }




