.swiper-container {
  height: 80%;
  cursor: none;
  z-index: 0; }

.swiper-element {
  text-align: center; }

.swiper-element__img {
  width: 58.565vw;
  height: 36.603vw; }

.swiper-slide.swiper-element {
  transform: translateY(0);
  transition: 1s; }

.swiper-slide.swiper-element.swiper-slide-active {
  transform: translateY(10.981vw); }

.swiper-slide.swiper-element.swiper-slide-prev {
  transform: translateY(5.490vw); }

@media (max-width: 979px) {
  .swiper-element__img {
    width: 78.087vw;
    height: 53.685vw; } }

@media (max-width: 640px) {
  .swiper-element__img {
    width: 78.087vw;
    height: 53.685vw; } }



