.footer {
  display: flex;
  justify-content: center;
  background-color: #FFFFFF;
  width: 100%;
  height: 20vh;
  position: relative;
  &__container {
    width: 90%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    &__copyright {
      font-family: Lato Regular;
      font-size: 1.171vw;
      line-height: 1.318vw;
      color: #AEAEAE; }
    &__link {
      font-size: 1.171vw;
      line-height: 1.318vw;
      color: #00A88E;
      text-decoration: none; } } }

@media (max-width: 979px) {
  .footer {
    height: 15vw; } }

@media (max-width: 640px) {
  .footer {
    height: 15vw;
    &__container {
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
      &__copyright {
        font-size: 3.748vw;
        line-height: 4.688vw; }
      &__link {
        font-size: 3.748vw;
        line-height: 4.688vw; } } } }
