.desing-card {
  display: flex;
  position: relative;
  justify-content: center;
  align-items: center;
  background: linear-gradient(0deg, #00A88E, #00A88E);
  border: 1px solid #FFFFFF;
  box-sizing: border-box;
  width: 29.283vw;
  height: 21.962vw;
  overflow: hidden;
  font-size: 0;
  z-index: 999;
  &__container {
    position: relative;
    width: 80%;
    height: 80%;
    z-index: 0;
    &__top {
      font-family: Lato Regular;
      position: absolute;
      top: 85%;
      transition: 0.5s;
      z-index: 30;
      &__name {
        font-size: 1.8vw;
        line-height: 2.8vw;
        color: #ffffff;
        margin-top: 0;
        margin-bottom: 10px; }
      &__date {
        font-size: 1.2vw;
        line-height: 1.5vw;
        color: #AEAEAE; } }
    & video {
      position: relative;
      width: 100%;
      height: 100%;
      z-index: 0;
      transform: scale(1.8); }
    &__bottom {
      position: absolute;
      bottom: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      &__download-text {
        font-family: Lato Bold;
        font-size: 1vw;
        line-height: 1vw;
        margin-right: 5px;
        opacity: 0;
        transition: 0.5s;
        & a {
          color: #ffffff;
          cursor: pointer;
          text-decoration: none; }
        & a::after {
          content: "";
          display: inline-block;
          margin-left: 5px;
          background-image: url("../../assets/images/downloadIconWhite.png");
          background-repeat: no-repeat;
          background-size: 1vw 1vw;
          width: 1vw;
          height: 1vw; } } } } }

.desing-card-bg {
  position: absolute;
  width: 100%;
  height: 100%;
  transform: scale(1.8);
  background-image: url(../../assets/images/cardVideoBgPng.png);
  background-repeat: no-repeat;
  background-position: center;
  z-index: 20; }

.top__active {
  top: -5%; }

.download-text__active {
  opacity: 1;
  z-index: 99; }

.preview__active {
  right: 0;
  z-index: 99; }

.bg__active {
  opacity: 0.9; }

@media (min-width: 1366px) {
  .desing-card {
    margin: 1px;
    overflow: hidden; }

  .desing-card-bg {
    transform: scale(4); } }

@media (max-width: 979px) {
  .desing-card {
    width: 44.792vw;
    height: 29.297vw;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 44.792vw 29.297vw;
    &__container {
      &__top {
        top: 0;
        &__name {
          font-size: 2.344vw;
          line-height: 2.604vw;
          margin-bottom: 10px; } }
      &__bottom {
        display: none; }
      & video {
        display: none; } } }

  .desing-card-bg {
    display: none; } }

@media (max-width: 640px) {
  .desing-card {
    width: 93.750vw;
    height: 56.250vw;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 93.750vw 56.250vw;
    &__container {
      &__top {
        top: 0;
        &__name {
          font-size: 5.500vw;
          line-height: 10.313vw;
          margin-bottom: 10px; } } } } }





