.maket {
  position: absolute;
  width: 30.015vw;
  height: 42.460vw;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  &__clock {
    position: absolute;
    top: 55%;
    left: 55%;
    width: 13.836vw;
    height: 16.252vw;
    transform: translateX(-50%) translateY(-50%) scale(1);
    z-index: 20;
    transition: 1s; }
  &__clock-shadow {
    position: absolute;
    top: 83%;
    left: 63%;
    width: 18.302vw;
    height: 6.296vw;
    transform: translateX(-50%) translateY(-20%) scale(1);
    z-index: 20; }
  &__background-layer {
    position: absolute;
    width: 100%;
    height: 100%;
    background-image: url("../../assets/images/maket/maketBackground.png");
    background-size: 100% 100%;
    background-repeat: no-repeat;
    background-position: center;
    z-index: 10;
    top: 0;
    left: 0;
    transition: 1s; }
  &__clock-layer {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 20;
    transition: 1s; }
  &__text-layer {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    position: absolute;
    width: 100%;
    height: 100%;
    background-image: url("../../assets/images/maket/maketText.png");
    background-size: 100% 100%;
    background-repeat: no-repeat;
    background-position: center;
    z-index: 30;
    top: 0;
    left: 0;
    transition: 1s;
    &__grid {
      width: 95%;
      height: 0;
      opacity: 0;
      margin-top: 3%;
      border-top: 0.146vw solid black;
      border-bottom: 0.146vw solid black;
      background-image: linear-gradient(90deg,#000 2px,transparent 0),linear-gradient(90deg,rgba(34,41,51,.62) 1px,transparent 0);
      background-size: 9.444vw 9.444vw, 2.306vw 2.306vw;
      transform: 0.5s; } }
  &__step2-text {
    position: absolute;
    top: 10%;
    left: 0;
    opacity: 0;
    transition: 0.5s;
    &__title {
      font-size: 2.635vw;
      line-height: 2.928vw;
      margin: 0; }
    &__line {
      font-size: 1.757vw;
      line-height: 2.416vw; }
    &__text {
      font-size: 1.757vw;
      line-height: 2.416vw;
      margin: 0; } }
  &__step3-text {
    position: absolute;
    top: 10%;
    right: 0;
    opacity: 0;
    transition: 0.5s;
    &__title {
      font-size: 2.635vw;
      line-height: 2.928vw;
      margin: 0; }
    &__line {
      font-size: 1.757vw;
      line-height: 2.416vw; }
    &__text {
      font-size: 1.757vw;
      line-height: 2.416vw;
      margin: 0; } } }

.background-layer__step1 {
  transform: skewY(-29.6deg) scaleX(0.9);
  top: -3.660vw;
  left: -18.302vw; }

.clock-layer__step1 {
  border: 1px solid black;
  transform: skewY(-29.6deg) scaleX(0.9); }

.text-layer__step1 {
  border: 1px solid black;
  transform: skewY(-29.6deg) scaleX(0.9);
  top: 3.660vw;
  left: 18.302vw; }

.clock__step1 {
  transform: translateX(-50%) translateY(-50%) scale(1.25); }

.clock-shadow__step1 {
  transform: translateX(-50%) translateY(-20%) scale(1.5); }

.background-layer__step2 {
  left: -29.283vw;
  opacity: 0; }

.step2-text__step2 {
  opacity: 1; }

.text-layer__step2 {
  left: 21.962vw; }

.text-layer__grid__step2 {
  animation-duration: 0.8s;
  animation-delay: 0.2s;
  animation-name: text-layer__grid__step2;
  animation-iteration-count: 1;
  animation-fill-mode: forwards; }
@keyframes text-layer__grid__step2 {
  from {
    height: 0%; }
  1% {
    opacity: 1; }
  to {
    height: 95%;
    opacity: 1; } }

.clock-layer__step2 {
  opacity: 0.6; }

.clock__step2 {
  opacity: 0.6;
  transform: translateX(-50%) translateY(-50%) scale(1.25); }

.clock-shadow__step2 {
  opacity: 0.6;
  transform: translateX(-50%) translateY(-20%) scale(1.5); }

.text-layer__step3 {
  left: 29.283vw;
  opacity: 0; }

.step2-text__step3 {
  opacity: 0; }

.step3-text__step3 {
  opacity: 1; }

.background-layer__step3 {
  top: -3.660vw;
  left: -18.302vw;
  opacity: 1; }

.btnBlock {
  position: absolute;
  bottom: -20%; }

@media (max-width: 640px) {
  .maket {
    width: 170px;
    height: 250px;
    &__clock {
      width: 80px;
      height: 95px; }
    &__clock-shadow {
      width: 180px;
      height: 100px; }
    &__text-layer {
      &__grid {
        border-top: 2px solid black;
        border-bottom: 2px solid black;
        background-size: 129px 129px,31.5px 31.5px; } } }

  .background-layer__step1 {
    left: -28.302vw; }
  .text-layer__step1 {
    left: 28.302vw; } }
